import { getItem } from "../Identifiers/ItemID";
import { getActor } from "../Identifiers/ActorID";
export async function addItem(itemData, actorId, qty) {
    let actor = await getActor(actorId);
    if (itemData.data.quantity !== qty) {
        itemData = {
            ...itemData,
            data: {
                ...itemData.data,
                quantity: qty
            }
        };
    }
    await actor.createOwnedItem(itemData);
}
export async function removeItem(itemId, qty) {
    let item = await getItem(itemId);
    if (!item)
        return null;
    let itemQty = item.data.data.quantity || 1;
    if (qty >= itemQty) {
        await item.delete({});
    }
    else {
        await item.update({ "data.quantity": itemQty - qty });
    }
    return item.data;
}
